.sign-header {
  display: flex;
  width: 100%;
  height: 80px;
  background-color: var(--sign-background-color);
  align-items: center;

  @media (max-width: 768px) {
    height: 60px;
  }

  .section {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: transparent;

    .logo {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      gap: 5px;

      .image {
        width: 24px;
        height: 24px;

        @media (max-width: 768px) {
          display: none;
        }
      }

      .title {
        font-size: 24px;
        font-weight: 500;
        line-height: 24px;

        @media (max-width: 768px) {
          display: none;
        }
      }

      .responsive-menu-btn {
        display: none;
        cursor: pointer;

        @media (max-width: 768px) {
          display: flex;
        }
      }
    }

    .menu-bar {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 30px;

      .item {
        text-decoration: none;
        text-transform: uppercase;
        position: relative;

        a {
          font-size: 18px;
          text-decoration: none;
          text-transform: uppercase;
        }

        button {
          font-size: 18px;
          text-decoration: none;
          text-transform: uppercase;
          cursor: pointer;
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          gap: 5px;

          * {
            color: inherit;
          }

          .arrow-btn {
            transition: 0.2s all ease;
            transform: rotate(-180deg);
          }
        }

        button.active {
          color: var(--menu-item-active-color);
        }

        a.active {
          color: var(--menu-item-active-color);
        }

        .dropdown-menu {
          display: block;
          position: absolute;
          background-color: var(--presale-image-border-color);
          box-shadow: 0 8px 16px rgba(131, 118, 118, 0.2);
          z-index: 1;
          list-style-type: none;
          padding: 0;
          margin: 0;
          top: 30px;
          left: 0;
          width: 100%;
          border-radius: 12px;
        }

        .dropdown-item {
          cursor: pointer;

          a {
            display: flex;
            padding: 10px;
            font-size: 16px;
            text-transform: capitalize;
          }
        }

        .dropdown-item:hover {
          color: var(--menu-item-active-color);
        }
      }

      @media (max-width: 768px) {
        display: none;
      }
    }

    .sign {
      display: flex;
      justify-content: center;
      align-items: center;

      .sign-btn {
        border-radius: 12px;
        background-color: var(--base-blue-color);
        border: 0px solid transparent;
        outline: none;
        height: 44px;
        padding: 7px 30px;
        font-size: 18px;
        cursor: pointer;
        transition: 0.2s all ease;

        @media (max-width: 768px) {
          height: 40px;
          padding: 5px 20px;
          font-size: 14px;
        }

        &:hover {
          opacity: 0.8;
        }
      }
    }
  }

  .responsive-menu {
    position: fixed;
    top: 0;
    left: -300px;
    width: 264px;
    height: 100vh;
    background-color: var(--background-color);
    transition: 0.2s all ease;
    z-index: 10;

    .responsive-section {
      position: relative;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 40px 20px;
      gap: 20px;

      .logo-section {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 5px;

        .logo-image {
          width: 36px;
          height: 36px;
        }

        .logo-title {
          font-size: 28px;
          line-height: 28px;
        }
      }

      .responsive-menu-list {
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 20px;
        gap: 15px;

        .menu-item {
          text-decoration: none;
          text-transform: uppercase;
          position: relative;
          transition: 0.2s all ease;
          gap: 15px;
          display: flex;
          flex-direction: column;

          .a-link {
            text-decoration: none;
          }

          .button-link {
            text-decoration: none;
            cursor: pointer;
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;

            * {
              color: inherit;
            }

            .arrow-btn {
              transition: 0.2s all ease;
              transform: rotate(-180deg);
            }
          }

          .button-link.active {
            color: var(--menu-item-active-color);
          }

          .a-link.active {
            color: var(--menu-item-active-color);
          }

          .drop-menu {
            width: 100%;
            padding-left: 10px;
            gap: 15px;
            display: flex;
            flex-direction: column;
          }
        }
      }

      .close-btn {
        position: absolute;
        top: 0;
        right: 0;
        border-top-right-radius: 6px;
        border-bottom-right-radius: 6px;
        width: 36px;
        height: 36px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;

        * {
          font-size: 16px;
        }
      }
    }
  }

  .responsive-menu.active {
    left: 0px;
  }
}
